import React, { useState } from "react";
import moment from "moment";
import { connect } from "react-redux";
import { IconButton } from "@material-ui/core";
import PropTypes from "prop-types";
import { Button } from "semantic-ui-react";
import url from "../../../../strings/url";
import {
  UpVote,
  RemoveUpVote,
  DownVote,
  RemoveDownVote,
} from "../../../../ApiRequests/request";
import {
  ArrowUpward,
  ArrowDownward,
  SentimentDissatisfied,
  Delete,
} from "@material-ui/icons";
import {
  Image,
  Card,
  Statistic,
  PlaceholderImage,
  Placeholder,
  Modal,
} from "semantic-ui-react";
import { blue, red } from "@material-ui/core/colors";

const fontStyle = {
  fontFamily: "poppins",
  fontSize: 12,
};
const fontStyle1 = {
  fontFamily: "poppins",
  fontSize: 15,
};

const Problem = (props) => {
  const [upVotes, setUpVotes] = useState(props.UpVotes);
  const [downVotes, setDownVotes] = useState(props.DownVotes);
  const [isUpVoted, setUpVote] = useState(props.UpVotedByMe);
  const [isDownVoted, setDownVote] = useState(props.DownVotedByMe);

  const HandleUpVote = () => {
    if (isDownVoted) {
      setDownVote(false);
      setDownVotes((state) => {
        return state - 1;
      });
      setUpVotes((state) => {
        return state + 1;
      });
      setUpVote((state) => {
        return true;
      });
      UpVote(props.id, localStorage.getItem("AnonymityToken#123")).then(
        (res) => {
          RemoveDownVote(props.id, localStorage.getItem("AnonymityToken#123"));
        }
      );
    } else if (!isUpVoted) {
      setUpVotes((state) => {
        return state + 1;
      });
      setUpVote((state) => {
        return true;
      });
      UpVote(props.id, localStorage.getItem("AnonymityToken#123"));
    } else if (isUpVoted) {
      setUpVotes((state) => {
        return state - 1;
      });
      setUpVote((state) => {
        return false;
      });
      RemoveUpVote(props.id, localStorage.getItem("AnonymityToken#123"));
    }
  };
  const handleDownVote = () => {
    if (isUpVoted) {
      setUpVote(false);
      setUpVotes((state) => {
        return state - 1;
      });
      setDownVotes((state) => {
        return state + 1;
      });
      setDownVote(true);
      DownVote(props.id, localStorage.getItem("AnonymityToken#123")).then(
        (res) => {
          return RemoveUpVote(
            props.id,
            localStorage.getItem("AnonymityToken#123")
          );
        }
      );
    } else if (!isDownVoted) {
      setDownVotes((state) => {
        return state + 1;
      });
      setDownVote(true);
      DownVote(props.id, localStorage.getItem("AnonymityToken#123"));
    } else if (isDownVoted) {
      setDownVotes((state) => {
        return state - 1;
      });
      setDownVote(false);
      RemoveDownVote(props.id, localStorage.getItem("AnonymityToken#123"));
    }
  };
  const deletePost = async () => {
    const headers = new Headers();
    const token = localStorage.getItem("AnonymityToken#123");
    headers.append("Authorization", token);
    headers.append("content-type", "application/json");
    const request = new Request(`${url}/user/post/${props.id}`, {
      method: "DELETE",
      headers,
    });
    try {
      const response = await fetch(request);
      if (response.status === 200) {
        window.location.reload(true);
      }
      setError(true);
    } catch (e) {
      setError(true);
    }
  };
  const [isImageLoading, setImage] = useState(true);
  const [isError, setError] = useState(false);
  const [showImageModal, setModal] = useState(false);
  const [showDeleteModal, setDeleteModal] = useState(false);

  return (
    <div>
      <Card
        style={{
          width: "100%",
          minWidth: 450,
          maxWidth: 700,
          marginBottom: 10,
        }}
      >
        <Card.Content>
          <Image
            src={props.profilePhoto}
            floated="left"
            size="mini"
            rounded
          ></Image>
          <Card.Header style={{ color: "grey", fontFamily: "poppins" }}>
            {props.username}
          </Card.Header>
          <Card.Meta style={{ color: "grey", fontFamily: "poppins" }}>
            {"Posted " +
              moment(props.createdAt).fromNow() +
              " \n" +
              "On " +
              moment(props.createdAt).format("LL")}
          </Card.Meta>
        </Card.Content>
        {isImageLoading ? (
          <Placeholder>
            <PlaceholderImage rectangular></PlaceholderImage>
          </Placeholder>
        ) : (
          ""
        )}
        <div
          onClick={() => {
            setModal(true);
          }}
        >
          <Image
            src={props.image}
            style={
              isImageLoading || isError
                ? { width: 0, height: 0 }
                : { width: "100%" }
            }
            onLoad={() => {
              setImage(false);
            }}
            onError={() => {
              setImage(false);
              setError(true);
            }}
          ></Image>
        </div>
        <Card.Content>
          <Card.Content>
            <div
              style={{
                fontFamily: "poppins",
                fontSize: 12,
                fontWeight: "bolder",
                color: "black",
              }}
            >
              Problem Statement
            </div>
          </Card.Content>
          <Card.Content>
            <Card.Description
              style={{
                fontWeight: "500",
                fontFamily: "poppins",
                wordWrap: "break-word",
                hyphens: "auto",
                marginTop: 10,
              }}
            >
              {props.description}
            </Card.Description>
          </Card.Content>
        </Card.Content>

        <Card.Content extra>
          {upVotes}
          <IconButton
            size="medium"
            style={{
              marginLeft: "0.5%",
              marginRight: "1%",
              color: isUpVoted ? blue[700] : "grey",
            }}
            onClick={() => {
              HandleUpVote();
            }}
          >
            <ArrowUpward></ArrowUpward>
          </IconButton>
          {downVotes}
          <IconButton
            size="medium"
            style={{
              marginLeft: "1%",
              color: isDownVoted ? red[500] : "grey",
            }}
            onClick={() => {
              handleDownVote();
            }}
          >
            <ArrowDownward></ArrowDownward>
          </IconButton>
          {props.hasDeleteButton ? (
            <IconButton
              onClick={() => {
                setDeleteModal(true);
              }}
              style={{ float: "right" }}
            >
              <Delete style={{ color: "red" }}></Delete>
            </IconButton>
          ) : (
            ""
          )}
        </Card.Content>
      </Card>
      <Modal
        open={showImageModal}
        basic
        onClose={() => {
          setModal(false);
        }}
        closeIcon
      >
        <Image src={props.image} centered></Image>
      </Modal>
      {props.hasDeleteButton ? (
        <Modal basic open={showDeleteModal}>
          <Modal.Header>Delete Your Problem</Modal.Header>
          <Modal.Content>
            <p>Are you sure you want to delete This Post ??</p>
          </Modal.Content>
          <Modal.Actions>
            <Button
              onClick={() => {
                setDeleteModal(false);
              }}
              color="red"
              inverted
            >
              No
            </Button>
            <Button
              positive
              onClick={() => {
                deletePost();
              }}
              color="green"
              inverted
            >
              Yes
            </Button>
          </Modal.Actions>
        </Modal>
      ) : (
        ""
      )}
    </div>
  );
};

export default connect()(Problem);
